// trannform inot a slug a given string
const slugyfy = (str) => {
  str = str.replace(/^\s+|\s+$/g, ""); // trim
  str = str.toLowerCase();

  // remove accents, swap ñ for n, etc
  var from = "àáäâèéëêìíïîòóöôùúüûñç·/_,:;";
  var to = "aaaaeeeeiiiioooouuuunc------";
  for (var i = 0, l = from.length; i < l; i++) {
    str = str.replace(new RegExp(from.charAt(i), "g"), to.charAt(i));
  }

  str = str
    .replace(/[^a-z0-9 -]/g, "") // remove invalid chars
    .replace(/\s+/g, "-") // collapse whitespace and replace by -
    .replace(/-+/g, "-"); // collapse dashes

  return str;
};

// Generate random ID
const generateID = () => {
  return Math.random().toString(36).substr(2, 14);
};

// Select random image from given set and params
const selectRandomImageFromSet = (
  obj,
  startingOwnedParam,
  numberOfTentatives
) => {
  let keys = Object.keys(obj);
  let randomIndex = keys[Math.floor(Math.random() * keys.length)];
  let item = obj[randomIndex];
  let tentatives = numberOfTentatives;
  let ownedParam = startingOwnedParam;
  if (item.owned > ownedParam) {
    // if the image is already owned by someone,
    // Check if the number of tentatives is the same of the object lenght.
    // this means that there are not available images and we need to pick up a copy
    if (tentatives === keys.length) {
      // We tried all the elements. Time to start with a copy image
      ownedParam++;
      return selectRandomImageFromSet(obj, ownedParam, 0);
    } else {
      // Just look for anothe item
      tentatives++;
      return selectRandomImageFromSet(obj, ownedParam, tentatives);
    }
  }
  return item;
};

const transformPlayersListIntoArray = (players) => {
  const entries = Object.entries(players);
  let transformedAry = [];
  entries.forEach((entry) => {
    transformedAry.push(entry[1]);
  });
  return transformedAry;
};

// get amount (MLN) left for given user (player key)
const getAmountLeftForPlayer = (playerKey, players, maxAmount) => {
  // Cicle through the object of players and get the purchased amount
  let currentAmount = 0;
  Object.keys(players).map(function (keyName, keyIndex) {
    if (players[keyName].purchased_by === playerKey) {
      return (currentAmount += players[keyName].purchased_for);
    }
  });
  return maxAmount - currentAmount;
};

// get max offer (MLN) for given user (player key)
const getMaxOfferFromUser = (
  playerKey,
  players,
  maxAmount,
  minNumOfPlayers
) => {
  if (!players) {
    return 0;
  }
  if (!minNumOfPlayers) {
    // from settings, there is not a minimum number of purchasable players.
    return getAmountLeftForPlayer(playerKey, players, maxAmount);
  }
  // Cicle through the object of players and get the purchased amount
  let currentAmount = 0;
  let currentNumOfPlayers = 0;
  Object.keys(players).map(function (keyName, keyIndex) {
    if (players[keyName].purchased_by === playerKey) {
      currentAmount += players[keyName].purchased_for;
      currentNumOfPlayers += 1;
      return;
    }
  });
  return (
    maxAmount - currentAmount - (minNumOfPlayers - currentNumOfPlayers - 1)
  );
};

// get amount of players (#) left to buy for given user (player key)
const getAmountOfPlayerLeftToBuyForUser = (
  playerKey,
  players,
  minNumOfPlayers
) => {
  // Cicle through the object of players and get the purchased players number
  let currentAmount = 0;
  if (!players) {
    return currentAmount;
  }
  Object.keys(players).map(function (keyName, keyIndex) {
    if (players[keyName].purchased_by === playerKey) {
      return (currentAmount += 1);
    }
  });
  return minNumOfPlayers - currentAmount;
};

// Get number of player purchased by given macro role
const purchasedByRole = (players, macrorole) => {
  let counter = 0;
  Object.keys(players).forEach(function (keyName, keyIndex) {
    if (
      players[keyName].macrorole === macrorole &&
      players[keyName].purchased_by &&
      players[keyName].purchased_by !== ""
    ) {
      counter++;
    }
  });
  return counter;
};

// Get current shuffle index
const getCurrentShuffleTimes = (players) => {
  let counter = 9999999999999999999;
  Object.keys(players).forEach(function (keyName, keyIndex) {
    if (players[keyName].shuffled_times <= counter) {
      counter = players[keyName].shuffled_times;
    }
  });
  return counter;
};

// FUnction used to pick up a player by given set and given params

// FUnction used to pick up a player by given set and given params
const shuffle = (
  players,
  settings,
  currentPlayerId = null,
  currentShuffleChunck = 0,
  numberOfTentatives = 0
) => {
  // Get a subset of items matching-criteria.
  // NOTE: This method is not really performance-aware, but with few players should be fine.
  let arraySubset = [];
  const porPurchased = purchasedByRole(players, "P");
  const defPurchased = purchasedByRole(players, "D");
  const cenPurchased = purchasedByRole(players, "C");
  const renewShuffleTreshold = settings.skip_times;
  Object.keys(players).forEach(function (keyName, keyIndex) {
    if (!players[keyName].purchased_by) {
      if (
        parseInt(players[keyName].shuffled_times) ===
        parseInt(currentShuffleChunck)
      ) {
        // Now i need to check if are estabilished some criteria for the shuffle
        switch (players[keyName].macrorole) {
          case "P":
            // P are always welcome. push it into the array
            arraySubset.push(players[keyName]);
            break;
          case "D":
            // Need to check if there is some threshold.
            if (porPurchased >= settings.random_shuffle_settings.def) {
              arraySubset.push(players[keyName]);
            }
            break;
          case "C":
            // Need to check if there is some threshold.
            if (porPurchased >= settings.random_shuffle_settings.def) {
              if (defPurchased >= settings.random_shuffle_settings.cen) {
                arraySubset.push(players[keyName]);
              }
            }
            break;
          case "A":
            // Need to check if there is some threshold.
            if (porPurchased >= settings.random_shuffle_settings.def) {
              if (defPurchased >= settings.random_shuffle_settings.cen) {
                if (cenPurchased >= settings.random_shuffle_settings.atk) {
                  arraySubset.push(players[keyName]);
                }
              }
            }
            break;
          default:
            break;
        }
      }
    }
  });

  // If the susbset array is empty, recall this function increasing the currentShuffleTimes
  if (!arraySubset.length) {
    const currentShuffleTimes = currentShuffleChunck + 1;

    return shuffle(
      players,
      settings,
      currentPlayerId,
      currentShuffleTimes,
      numberOfTentatives
    );
  } else {
    // Return random id
    const x = arraySubset[Math.floor(Math.random() * arraySubset.length)];
    return x;
  }
};

