const canUserBuyPlayer = (amountToCompare, settings, players, user) => {
  let maxOffer = getMaxOfferFromUser(
    user,
    players,
    settings.user_mln,
    settings.min_players
  );
  let minPlayersLeft = getAmountOfPlayerLeftToBuyForUser(
    user,
    players,
    settings.min_players
  );
  let maxPlayersLeft = getAmountOfPlayerLeftToBuyForUser(
    user,
    players,
    settings.max_players
  );

  if (parseInt(minPlayersLeft) === parseInt(maxPlayersLeft)) {
    // We have a fixed amout of players
    if (amountToCompare <= maxOffer && minPlayersLeft > 0) {
      return true;
    } else {
      return false;
    }
  } else {
    // We donìt have the same amount of player.
    if (parseInt(minPlayersLeft) <= 0) {
      // User bought the min num of players. Calculate maxoffer as the amount left that user can use for a single player
      let spentAmount = 0;
      Object.keys(players).map(function (keyName, keyIndex) {
        if (players[keyName].purchased_by === user) {
          spentAmount += parseInt(players[keyName].purchased_for);
          return;
        }
      });
      const amountLeft = parseInt(settings.user_mln) - parseInt(spentAmount);
      if (amountToCompare <= amountLeft && maxPlayersLeft > 0) {
        return true;
      } else {
        return false;
      }
    } else {
      // User still needs to buy the min amount of players
      if (amountToCompare <= maxOffer && minPlayersLeft > 0) {
        return true;
      } else {
        return false;
      }
    }
  }
};

const getBestRoleForPlayer = (player) => {
  let role = "";
  let roleWeight = 99;
  const mantraRoles = player.role.split(";");
  if (mantraRoles.length === 1) {
    // Player has just one role, return it!
    return mantraRoles[0];
  }
  mantraRoles.map((currentRole) => {
    if (roleWeights[currentRole.toUpperCase()] < roleWeight) {
      role = currentRole;
      roleWeight = roleWeights[currentRole.toUpperCase()];
    }
  });
  return role;
};

