const getUserByID = (id, users) => {
  users = transformPlayersListIntoArray(users);
  var user = users.filter((obj) => {
    return obj.id === id;
  })[0];
  return user;
};

function getKeyByValue(object, value) {
  return Object.keys(object).find((key) => object[key] === value);
}

