// Import react dependecies
import React from "react";
import { useSelector, useDispatch } from "react-redux";
import { useState, useRef, useEffect } from "react";
import { CircularProgressbar, buildStyles } from "react-circular-progressbar";
// import helpers
import {
  getAmountOfPlayerLeftToBuyForUser,
  getMaxOfferFromUser,
  shuffle,
  canUserBuyPlayer,
  getAmountLeftForPlayer,
  getUserByID,
} from "../../Helpers/helpers";

// Import classes
import "./Player.css";
import { Select, MenuItem } from "@material-ui/core";

// Import components
import PlayerCard from "../UI/Player/PlayerCard";
import PlayerStatistics from "../UI/Player/PlayerStatistics";

// IMport material UI
import PropTypes from "prop-types";
import Tabs from "@mui/material/Tabs";
import Tab from "@mui/material/Tab";
import Typography from "@mui/material/Typography";
import Box from "@mui/material/Box";
import TabPanel from "../UI/TabPanel";

import { Row, Col, Container } from "react-bootstrap";

import caneURL from "../../Assets/sounds/cane.ogg";
import devastanteURL from "../../Assets/sounds/devastante.ogg";
import sennoURL from "../../Assets/sounds/senno.ogg";
import impazzitoURL from "../../Assets/sounds/impazzito.ogg";
import incredibileURL from "../../Assets/sounds/incredibile.ogg";
import porcaURL from "../../Assets/sounds/porca.ogg";
import veramenteURL from "../../Assets/sounds/veramente.ogg";
import peresURL from "../../Assets/sounds/peres.ogg";
import pazziURL from "../../Assets/sounds/pazzi.ogg";
import acquistoURL from "../../Assets/sounds/acquisto.ogg";
import ingiustoURL from "../../Assets/sounds/ingiusto.ogg";
import onestoURL from "../../Assets/sounds/onesto.ogg";
import cadavereURL from "../../Assets/sounds/cadavere.ogg";
import cagareURL from "../../Assets/sounds/cagare.ogg";
import madreURL from "../../Assets/sounds/madre.ogg";
import dioURL from "../../Assets/sounds/dio.ogg";
import mancosuURL from "../../Assets/sounds/mancosu.ogg";
import cinqueURL from "../../Assets/sounds/5.ogg";
import jayURL from "../../Assets/sounds/j.ogg";
import elementariURL from "../../Assets/sounds/elementari.ogg";
import pogbaURL from "../../Assets/sounds/pogba.ogg";

// import helpers
// import {
//   slugyfy,
//   generateID,
//   selectRandomImageFromSet,
// } from "../../Helpers/helpers";

const Player = (props) => {
  // add useselector hook for data
  const players = useSelector((state) => (state ? state.players : ""));
  const settings = useSelector((state) => (state ? state.settings : ""));
  const users = useSelector((state) => (state ? state.users : ""));

  const valueInput = useRef();
  const [valueInputValue, setValueInputValue] = useState(1);
  const [buyer, setBuyer] = useState("");
  const [tabValue, setTabValue] = React.useState(0);

  // Set useState vars to modify price and user Owner (just for modify)
  const [purchasedPrice, setPurchasedPrice] = useState();
  const [userOwner, setUserOwner] = useState();

  // set timer var with useSTate
  // const [timerValue, setTimerValue] = useState(0);

  // Set Error and success useState
  const [errorString, setErrorString] = useState("");
  const [successString, setSuccessString] = useState("");
  const [purchasedString, setPurchasedString] = useState("");

  // Set button mofify visibility
  const [confirmModButton, setConfirmModButton] = useState(false);

  // Set new player (used for shuffle)
  const [player, setPlayer] = useState(() => {
    // Get player
    let x;
    Object.keys(players).forEach(function (key) {
      if (parseInt(players[key].id) === parseInt(props.playerId)) {
        x = players[key];
      }
    });
    return x;
  });

  const handleTabChange = (event, newValue) => {
    setTabValue(newValue);
  };

  function a11yProps(index) {
    return {
      id: `simple-tab-${index}`,
      "aria-controls": `simple-tabpanel-${index}`,
    };
  }

  // add distaptch
  const dispatch = useDispatch();

  // Set canvas
  let canvas;

  // Handle error
  if (!player || !Object.keys(player).length) {
    // @todo handle error, return error message
    return <div>Error</div>;
  }

  // Set sticker
  let sticker = "acquisto.png";
  if (player.purchased_by !== "") {
    if (player.name === "El Shaarawy") {
      sticker = "carles.png";
    } else if (
      player.name === "Orsolini" ||
      player.name === "Rovella" ||
      player.name === "Mckennie" ||
      player.name === "Rabiot" ||
      player.name === "Rugani" ||
      player.name === "De Sciglio" ||
      player.name === "Frabotta"
    ) {
      sticker = "madre.png";
    } else if (player.name === "Kovalenko") {
      sticker = "mancosu.png";
    } else if (player.name === "Gagliardini") {
      sticker = "cadavere.png";
    } else if (player.name === "Caldirola") {
      sticker = "elementari.png";
    } else if (player.name === "Mario Rui") {
      sticker = "cagare.png";
    } else if (player.name === "Pogba") {
      sticker = "pogba.png";
    } else if (
      player.name === "Cristante" ||
      player.name === "Biraghi" ||
      player.name === "Destro" ||
      player.name === "Soriano" ||
      player.name === "Hysaj"
    ) {
      sticker = "5.png";
    } else if (
      (player.name === "Caprari" ||
        player.name === "Mario Rui" ||
        player.name === "Nzola" ||
        player.name === "Demme" ||
        player.name === "Lobotka" ||
        player.name === "Calhanoglu" ||
        player.name === "Romagnoli") &&
      player.purchased_by !== "tia" &&
      player.purchased_by !== "mattia" &&
      player.purchased_by !== "punta" &&
      player.purchased_by !== "mattia_puntarello"
    ) {
      sticker = "ingiusto.png";
    } else if (
      player.purchased_for >= 45 &&
      player.purchased_for < 60 &&
      player.marketvalue < 23 &&
      player.macrorole !== "P"
    ) {
      sticker = "pazzi.png";
    } else if (player.purchased_for >= 190) {
      sticker = "devastante.png";
    } else if (player.purchased_for >= 140) {
      sticker = "incredibile.png";
    } else if (
      (player.team.toLowerCase() === "salernitana" ||
        player.team.toLowerCase() === "spezia" ||
        player.team.toLowerCase() === "empoli" ||
        player.team.toLowerCase() === "cremonese" ||
        player.team.toLowerCase() === "lecce") &&
      player.purchased_for < 6 &&
      player.marketvalue < 15
    ) {
      sticker = "cadavere.png";
    } else if (
      (player.team.toLowerCase() === "salernitana" ||
        player.team.toLowerCase() === "spezia" ||
        player.team.toLowerCase() === "empoli" ||
        player.team.toLowerCase() === "cremonese" ||
        player.team.toLowerCase() === "lecce") &&
      player.purchased_for >= 6 &&
      player.marketvalue < 15
    ) {
      sticker = "cagare.png";
    } else if (
      (player.purchased_for < parseInt(player.marketvalue) * 1.5 &&
        player.marketvalue >= 23) ||
      (player.macrorole === "A" &&
        player.marketvalue >= 20 &&
        player.purchased_for < parseInt(player.marketvalue) * 3)
    ) {
      sticker = "acquisto.png";
    } else if (
      (player.purchased_for < parseInt(player.marketvalue) * 2 &&
        player.marketvalue >= 23) ||
      (player.macrorole === "A" &&
        player.marketvalue >= 20 &&
        player.purchased_for < parseInt(player.marketvalue) * 4)
    ) {
      sticker = "onesto.png";
    } else if (
      parseInt(player.marketvalue) < 5 &&
      player.macrorole !== "P" &&
      (player.team.toLowerCase() === "bologna" ||
        player.team.toLowerCase() === "cagliari" ||
        player.team.toLowerCase() === "genoa" ||
        player.team.toLowerCase() === "verona")
    ) {
      sticker = "j.png";
    } else if (
      parseInt(player.marketvalue) < 5 &&
      player.macrorole !== "P" &&
      player.purchased_for > 1
    ) {
      sticker = "veramente.png";
    } else if (
      parseInt(player.marketvalue) < 6 &&
      player.macrorole === "P" &&
      player.purchased_for > 1
    ) {
      sticker = "ingiusto.png";
    } else if (
      parseInt(player.marketvalue) >= 15 &&
      player.macrorole === "P" &&
      player.purchased_for <= 40
    ) {
      sticker = "acquisto.png";
    } else if (
      parseInt(player.marketvalue) >= 15 &&
      player.macrorole === "P" &&
      player.purchased_for <= 55
    ) {
      sticker = "onesto.png";
    } else if (
      parseInt(player.marketvalue) > 12 &&
      player.macrorole === "P" &&
      player.purchased_for > 30
    ) {
      sticker = "senno.png";
    } else if (player.purchased_for >= parseInt(player.marketvalue) * 3) {
      sticker = "dio.png";
    } else if (player.purchased_for >= parseInt(player.marketvalue) * 2.6) {
      sticker = "impazzito.png";
    } else if (player.purchased_for >= parseInt(player.marketvalue) * 2.2) {
      sticker = "cane.png";
    } else if (player.purchased_for >= parseInt(player.marketvalue) * 1.8) {
      sticker = "porca.png";
    } else if (player.purchased_for >= parseInt(player.marketvalue) * 1.4) {
      sticker = "senno.png";
    } else if (player.purchased_for > parseInt(player.marketvalue)) {
      sticker = "onesto.png";
    }
  }

  const getAudioUrl = (
    purchasedPrice,
    marketValue,
    nameOfPlayer,
    macrorole,
    team,
    buyer
  ) => {
    let audioUrl = acquistoURL;
    if (purchasedPrice !== "") {
      if (nameOfPlayer === "El Shaarawy") {
        audioUrl = peresURL;
      } else if (
        nameOfPlayer === "Orsolini" ||
        nameOfPlayer === "Rovella" ||
        nameOfPlayer === "Mckennie" ||
        nameOfPlayer === "Rabiot" ||
        nameOfPlayer === "Rugani" ||
        nameOfPlayer === "De Sciglio" ||
        nameOfPlayer === "Frabotta"
      ) {
        audioUrl = madreURL;
      } else if (nameOfPlayer === "Kovalenko") {
        audioUrl = mancosuURL;
      } else if (nameOfPlayer === "Gagliardini") {
        audioUrl = cadavereURL;
      } else if (nameOfPlayer === "Pogba") {
        audioUrl = pogbaURL;
      } else if (nameOfPlayer === "Mario Rui") {
        audioUrl = cagareURL;
      } else if (nameOfPlayer === "Caldirola") {
        audioUrl = elementariURL;
      } else if (
        nameOfPlayer === "Cristante" ||
        nameOfPlayer === "Biraghi" ||
        nameOfPlayer === "Destro" ||
        nameOfPlayer === "Soriano" ||
        nameOfPlayer === "Hysaj"
      ) {
        audioUrl = cinqueURL;
      } else if (
        (nameOfPlayer === "Caprari" ||
          nameOfPlayer === "Nzola" ||
          nameOfPlayer === "Demme" ||
          nameOfPlayer === "Lobotka" ||
          nameOfPlayer === "Calhanoglu" ||
          nameOfPlayer === "Romagnoli") &&
        buyer !== "tia" &&
        buyer !== "mattia" &&
        buyer !== "punta" &&
        buyer !== "mattia_puntarello"
      ) {
        audioUrl = ingiustoURL;
      } else if (
        purchasedPrice >= 45 &&
        purchasedPrice < 60 &&
        marketValue < 23 &&
        macrorole !== "P"
      ) {
        audioUrl = pazziURL;
      } else if (purchasedPrice >= 190) {
        audioUrl = devastanteURL;
      } else if (purchasedPrice >= 140) {
        audioUrl = incredibileURL;
      } else if (
        (team.toLowerCase() === "salernitana" ||
          team.toLowerCase() === "spezia" ||
          team.toLowerCase() === "empoli" ||
          team.toLowerCase() === "cremonese" ||
          team.toLowerCase() === "lecce") &&
        purchasedPrice < 6 &&
        marketValue < 15
      ) {
        audioUrl = cadavereURL;
      } else if (
        (team.toLowerCase() === "salernitana" ||
          team.toLowerCase() === "spezia" ||
          team.toLowerCase() === "empoli" ||
          team.toLowerCase() === "cremonese" ||
          team.toLowerCase() === "lecce") &&
        purchasedPrice >= 6 &&
        marketValue < 15
      ) {
        audioUrl = cagareURL;
      } else if (
        (purchasedPrice < parseInt(marketValue) * 1.5 && marketValue >= 23) ||
        (macrorole === "A" &&
          marketValue >= 20 &&
          purchasedPrice < parseInt(marketValue) * 3)
      ) {
        audioUrl = acquistoURL;
      } else if (
        (purchasedPrice < parseInt(marketValue) * 2 && marketValue >= 23) ||
        (macrorole === "A" &&
          marketValue >= 20 &&
          purchasedPrice < parseInt(marketValue) * 4)
      ) {
        audioUrl = onestoURL;
      } else if (
        parseInt(marketValue) < 5 &&
        macrorole !== "P" &&
        (team.toLowerCase() === "bologna" ||
          team.toLowerCase() === "cagliari" ||
          team.toLowerCase() === "genoa" ||
          team.toLowerCase() === "verona")
      ) {
        audioUrl = jayURL;
      } else if (
        parseInt(marketValue) < 5 &&
        macrorole !== "P" &&
        purchasedPrice > 1
      ) {
        audioUrl = veramenteURL;
      } else if (
        parseInt(marketValue) < 6 &&
        macrorole === "P" &&
        purchasedPrice > 1
      ) {
        audioUrl = ingiustoURL;
      } else if (
        parseInt(marketValue) >= 15 &&
        macrorole === "P" &&
        purchasedPrice <= 40
      ) {
        audioUrl = acquistoURL;
      } else if (
        parseInt(marketValue) >= 15 &&
        macrorole === "P" &&
        purchasedPrice <= 55
      ) {
        audioUrl = onestoURL;
      } else if (
        parseInt(marketValue) > 12 &&
        macrorole === "P" &&
        purchasedPrice > 30
      ) {
        audioUrl = sennoURL;
      } else if (purchasedPrice >= parseInt(marketValue) * 3) {
        audioUrl = dioURL;
      } else if (purchasedPrice >= parseInt(marketValue) * 2.6) {
        audioUrl = impazzitoURL;
      } else if (purchasedPrice >= parseInt(marketValue) * 2.2) {
        audioUrl = caneURL;
      } else if (purchasedPrice >= parseInt(marketValue) * 1.8) {
        audioUrl = porcaURL;
      } else if (purchasedPrice >= parseInt(marketValue) * 1.4) {
        audioUrl = sennoURL;
      } else if (purchasedPrice > parseInt(marketValue)) {
        audioUrl = onestoURL;
      }
      return audioUrl;
    }
  };

  // Get team IMG
  const teamImage = require("../../Assets/stemmi/" +
    player.team +
    ".png").default;

  // Player Name
  let namesArray = player.name.split(" ");
  namesArray.forEach((element, index) => {
    namesArray[index] =
      namesArray[index].charAt(0) + namesArray[index].slice(1).toLowerCase();
  });
  const playerName = namesArray.join(" ");

  // Get mantra roles Array
  const mantraRolesAry = player.role.split(";");

  // input handlers
  const onIncrease = () => {
    if (valueInputValue + 1 < settings.user_mln) {
      setValueInputValue(valueInputValue + 1);
    }
  };
  const onDecrease = () => {
    if (valueInputValue + -1 > 0) {
      setValueInputValue(valueInputValue - 1);
    }
  };

  const onHandleValueChange = (e) => {
    e.preventDefault();
    const value = Number(e.target.value);
    if (value > 0 && value < settings.user_mln) {
      setValueInputValue(value);
    }
  };

  // Handle Buyer select onChange
  const handleBuyerChange = (event) => {
    setBuyer(event.target.value);
    // paste into the select
    let element = document
      .getElementById("buyer")
      .getElementsByTagName("span")[0];
    if (element && element.length) {
      element.textContent = users[event.target.value].team;
    }
  };

  // Handle purchased price onChange modifier
  const handleModifyPrice = (event) => {
    setPurchasedPrice(event.target.value);
    setErrorString("");
    setSuccessString("");
    setConfirmModButton(true);
    if (!userOwner) {
      setUserOwner(player.purchased_by);
    }
  };

  // Handle modify user owner
  const handleModifyNewOwnerUser = (event) => {
    setUserOwner(event.target.value);
    setErrorString("");
    setSuccessString("");
    setConfirmModButton(true);
    if (!purchasedPrice) {
      setPurchasedPrice(player.purchased_for);
    }
  };

  // purchasePlayer
  const purchasePlayer = () => {
    const value = Math.floor(valueInputValue);
    if (!value > 0) {
      //@todo handle error
      return;
    }
    if (!buyer) {
      //@todo handle error
      return;
    }

    // Fire audio
    const audioUrl = getAudioUrl(
      valueInputValue,
      player.marketvalue,
      player.name,
      player.macrorole,
      player.team,
      buyer
    );
    new Audio(audioUrl).play();

    // send action
    dispatch({
      type: "purchase_player",
      user: buyer,
      mln: valueInputValue,
      playerId: player.id,
    });

    setPurchasedString(
      `Congratulazioni! ${player.name} è un nuovo calciatore di ${users[buyer].team}.`
    );
  };

  // modify player owner data
  const modifyPlayer = () => {
    const maxOffer = getMaxOfferFromUser(
      userOwner,
      players,
      settings.user_mln,
      settings.min_players
    );
    const ownerMaxOffer =
      parseInt(maxOffer - 1) + parseInt(player.purchased_for);
    if (fantauser && fantauser.role !== "admin") {
      setErrorString(`Non hai i permessi per modificare questo calciatore.`);
      return;
    } else if (
      player.purchased_by === userOwner &&
      purchasedPrice > ownerMaxOffer
    ) {
      setErrorString(
        `La massima offerta fruibile da ${users[userOwner].team} è di ${ownerMaxOffer}mln`
      );
      return;
    } else if (player.purchased_by !== userOwner && purchasedPrice > maxOffer) {
      // Check if user has funds for it
      setErrorString(
        `La massima offerta fruibile da ${users[userOwner].team} è di ${maxOffer}mln`
      );
      return;
    } else if (purchasedPrice < 1) {
      setErrorString(`Seleziona un valore valido.`);
      return;
    } else if (
      !canUserBuyPlayer(purchasedPrice, settings, players, userOwner) &&
      player.purchased_by !== userOwner
    ) {
      // If I am trying to modyfy the player, check that he has enough slots.
      //if player.purchased_by !== userOwner the user is different. So no need to check for this
      setErrorString(
        ` ${users[userOwner].team} non può acquistare altri calciatori. Svincola un calciatore prima di procedere.`
      );
      return;
    }
    // DIspatch
    dispatch({
      type: "update_player_user",
      newPrice: purchasedPrice,
      newOwner: userOwner,
      playerId: player.id,
    });

    setSuccessString("Dati modificati correttamente.");
    setPurchasedString("");
    setConfirmModButton(false);
  };

  // Shuffle player
  const shufflePlayer = () => {
    setSuccessString("");
    setErrorString("");
    setPurchasedString("");
    // document.getElementById('teamBadge').classList.add("slide");
    // Hide Canvas
    if (canvas) {
      canvas.style.display = "none";
    }

    setPurchasedString("");

    // get shuffled player
    const shuffledPlayer = shuffle(players, settings, player.id);

    // Update player shuffled_times.
    dispatch({
      type: "shuffle_update",
      playerId: shuffledPlayer.id,
    });

    // Execute shuffle
    setPlayer(shuffledPlayer);
    setValueInputValue(1);
    setBuyer("");
  };

  // Init canvas listener
  const initCanvasListener = () => {
    canvas = document.querySelector("#wrapper");
  };

  const setPlayerFree = (id, name, mln) => {
    if (
      window.confirm(
        `🤔 Sei sicuro di voler svincolare ${name} per ${mln} mln? 🤔`
      )
    ) {
      if (canvas) {
        canvas.style.display = "none";
      }
      dispatch({ type: "set_player_free", id: id });
      setSuccessString("");
      setPurchasedString("");
    }
  };

  // Get logged user
  const fantauser = JSON.parse(window.localStorage.getItem("fantauser"));

  return (
    <div className={"wrapper"}>
      <canvas id="wrapper"></canvas>
      {initCanvasListener()}

      <canvas id="wrapper"></canvas>
      <div className="player__headerWrapper">
        <div className="player__teamBadge">
          <img
            src={teamImage}
            className={"teamImage"}
            title={player.team}
            alt={player.team}
          />
        </div>
        <div className={"header " + player.team}>
          <h2 className={"title"}>
            {player.fullname ? player.fullname : player.name}
          </h2>
        </div>
      </div>
      <div className="player__decoration"></div>

      <div className="mt-3">
        <div className="d-md-flex">
          <PlayerCard player={player} />
          <div className="w-100 ml-30px">
            <Row>
              <Col>
                <Box sx={{ width: "100%", padding: "0px !important" }}>
                  <Box
                    sx={{
                      borderBottom: 1,
                      borderColor: "divider",
                      padding: "0px !important",
                      backgroundColor: "#414141",
                    }}
                  >
                    <Tabs
                      value={tabValue}
                      onChange={handleTabChange}
                      aria-label="basic tabs example"
                    >
                      <Tab
                        sx={{
                          padding: "20px !important",
                          color: "white !important",
                        }}
                        label="Asta"
                        {...a11yProps(0)}
                      />
                      {/* <Tab
                        sx={{
                          padding: "20px !important",
                          color: "white !important",
                        }}
                        label="Statistiche"
                        {...a11yProps(0)}
                      /> */}
                    </Tabs>
                  </Box>
                  <TabPanel value={tabValue} index={0}>
                    <Row>
                      <Col xs={12} lg={7}>
                        <Row>
                          {(player.purchased_by === "" ||
                            !player.purchased_by) && (
                            <Col>
                              <div className="actions">
                                <h5 className="mb-0">Asta corrente</h5>
                                <div className="d-flex align-items-end">
                                  <p
                                    className={"currentValue"}
                                    style={{ textAlign: "center" }}
                                  >
                                    {valueInputValue}
                                  </p>
                                  <span className={"marketCurrency"}>mln</span>
                                </div>

                                {fantauser &&
                                  (fantauser.role === "admin" ||
                                    fantauser.role === "superadmin") && (
                                    <div className={"inputSelectorWrapper"}>
                                      <button
                                        className={"minus"}
                                        onClick={onDecrease}
                                      >
                                        -
                                      </button>
                                      <input
                                        type="number"
                                        ref={valueInput}
                                        defaultValue={valueInputValue}
                                        className={"selector"}
                                        onChange={onHandleValueChange}
                                        min="1"
                                        step="1"
                                        max={settings.base_wallet}
                                      ></input>
                                      <button
                                        className={"plus"}
                                        onClick={onIncrease}
                                      >
                                        +
                                      </button>
                                    </div>
                                  )}

                                <div className="buyerWrapper">
                                  <label className="custom-label">
                                    Seleziona fantallenatore
                                  </label>
                                  <Select
                                    labelId="buyer_label"
                                    id="buyer"
                                    value={buyer ? buyer : ""}
                                    onChange={handleBuyerChange}
                                  >
                                    {Object.keys(users).map(function (
                                      keyName,
                                      keyIndex
                                    ) {
                                      if (
                                        canUserBuyPlayer(
                                          valueInputValue,
                                          settings,
                                          players,
                                          keyName
                                        )
                                      ) {
                                        return (
                                          <MenuItem
                                            key={keyName}
                                            value={keyName}
                                          >
                                            {users[keyName].team}
                                          </MenuItem>
                                        );
                                      }
                                    })}
                                  </Select>
                                  {buyer && (
                                    <button
                                      className="primaryButton mt-4"
                                      onClick={purchasePlayer}
                                    >
                                      <svg
                                        viewBox="0 0 511.999 511.999"
                                        width="15"
                                        height="15"
                                        style={{ marginRight: "5px" }}
                                      >
                                        <path d="M117.776 174.345a10.01 10.01 0 0 0-14.139 0 9.998 9.998 0 1 0 14.139 14.14c3.909-3.9 3.909-10.23 0-14.14zm135.423 273.373c4.409-5.972 6.792-13.167 6.792-20.732 0-19.298-15.7-34.999-34.999-34.999H54.998a34.76 34.76 0 0 0-24.749 10.249 34.76 34.76 0 0 0-10.25 24.75c0 7.749 2.536 14.915 6.815 20.72C10.891 456.078 0 472.778 0 491.983v10c0 5.523 4.478 10 10 10h259.99c5.522 0 10-4.477 10-10v-10c0-19.196-10.88-35.889-26.791-44.265zM44.39 416.376a14.9 14.9 0 0 1 10.608-4.391h169.994a15.02 15.02 0 0 1 14.999 14.999 14.9 14.9 0 0 1-15 15H54.998a15.02 15.02 0 0 1-14.999-14.999c0-4.009 1.559-7.777 4.391-10.609zm-24.391 75.607c0-16.541 13.458-29.999 29.999-29.999h5 169.994 5c16.541 0 29.999 13.458 29.999 29.999H19.999zm482.694-71.859c-3.396-5.263-7.448-9.917-12.045-13.833l-46.725-40.753a10.07 10.07 0 0 0-1.269-1.551c-.812-.811-1.732-1.444-2.71-1.918l-28.257-24.645-130.79-114.091a29.75 29.75 0 0 0 8.302-20.699 29.81 29.81 0 0 0-8.794-21.225 29.69 29.69 0 0 0-8.291-5.837l12.1-12.1c3.382.899 6.857 1.359 10.335 1.359 10.241-.001 20.483-3.899 28.279-11.695 7.558-7.557 11.72-17.602 11.72-28.285s-4.162-20.728-11.719-28.285L237.981 11.72A39.74 39.74 0 0 0 209.692.001c-10.681 0-20.724 4.162-28.28 11.719-10.444 10.444-13.882 25.273-10.336 38.615L50.351 171.059c-3.336-.887-6.807-1.355-10.353-1.355-10.688 0-20.729 4.159-28.28 11.709-15.596 15.596-15.596 40.973 0 56.57l84.847 84.848c7.799 7.798 18.042 11.697 28.285 11.697s20.487-3.899 28.286-11.697c10.444-10.445 13.882-25.273 10.336-38.615l12.075-12.076c1.451 3.03 3.42 5.826 5.866 8.271a29.8 29.8 0 0 0 21.215 8.787 29.79 29.79 0 0 0 20.696-8.291L362.63 440.961c.384.6.832 1.171 1.356 1.695a9.76 9.76 0 0 0 .558.504l41.511 47.694a58.96 58.96 0 0 0 3.28 3.561c11.401 11.397 26.46 17.582 42.27 17.582 2.787 0 5.6-.192 8.417-.582 18.829-2.606 35.102-13.87 44.644-30.908 10.453-18.673 9.675-42.38-1.973-60.383zM195.555 25.862c3.779-3.779 8.801-5.861 14.138-5.861 5.344 0 10.369 2.082 14.147 5.861l84.848 84.847a19.87 19.87 0 0 1 5.86 14.143c0 5.341-2.081 10.364-5.861 14.143-7.795 7.797-20.481 7.797-28.276 0L195.56 54.144c-7.802-7.801-7.802-20.486-.005-28.282zm-56.561 282.827a20.03 20.03 0 0 1-28.287 0L25.86 223.842c-7.799-7.799-7.799-20.487 0-28.287 3.772-3.773 8.794-5.851 14.138-5.851s10.362 2.077 14.133 5.847l84.862 84.862c7.798 7.795 7.798 20.481.001 28.276zm42.102-70.38l-27.961 27.962-84.856-84.856L181.412 68.28l84.855 84.856-28.012 28.013-.276.265-56.538 56.539c-.117.115-.23.235-.345.356zm35.706 20.861l-.059.059-7.042 7.042a9.94 9.94 0 0 1-7.074 2.928c-2.673 0-5.185-1.04-7.073-2.929a9.94 9.94 0 0 1-2.932-7.079c0-2.618.999-5.081 2.812-6.939l56.801-56.801c3.893-3.786 10.187-3.739 14.034.104 1.89 1.89 2.931 4.404 2.931 7.079s-1.038 5.18-2.932 7.069l-7.001 7.001-.143.143-42.322 42.323zm20.708 7.575l29.226-29.226 123.742 107.935-45.154 45.155L237.51 266.745zm120.975 138.986l47.099-47.1 15.107 13.177-49.045 49.046-13.161-15.123zm128.732 65.006c-6.454 11.523-17.365 19.128-29.936 20.868-12.542 1.736-24.865-2.395-33.807-11.333a38.42 38.42 0 0 1-2.191-2.381l-.097-.113-36.38-41.802 50.991-50.992 41.75 36.417c.031.028.063.055.096.083 3.125 2.657 5.898 5.848 8.251 9.494 7.562 11.685 8.092 27.665 1.323 39.759zM188.484 103.636c-3.906-3.905-10.236-3.905-14.142 0l-42.428 42.428a10 10 0 0 0 0 14.141c1.953 1.953 4.512 2.929 7.071 2.929a9.97 9.97 0 0 0 7.071-2.929l42.428-42.428a10 10 0 0 0 0-14.141z" />
                                      </svg>
                                      Aggiudica
                                    </button>
                                  )}
                                </div>
                              </div>
                            </Col>
                          )}
                          {player.purchased_by !== "" && (
                            <div>
                              <div className="actions">
                                {purchasedString !== "" && (
                                  <div>
                                    <p
                                      className="success"
                                      style={{
                                        marginTop: "10px",
                                        marginBottom: "10px",
                                        textAlign: "center",
                                      }}
                                    >
                                      {purchasedString}
                                    </p>
                                    <img
                                      src={
                                        require("../../Assets/confetti.gif")
                                          .default
                                      }
                                      alt=""
                                      className="confetti"
                                    />
                                  </div>
                                )}
                                {fantauser.role === "admin" && (
                                  <>
                                    <h4>Modifica</h4>
                                    <div
                                      className="flex"
                                      style={{ justifyContent: "center" }}
                                    >
                                      <input
                                        type="number"
                                        defaultValue={player.purchased_for}
                                        min="1"
                                        step="1"
                                        max={settings.user_mln}
                                        className="modifyPrice"
                                        onChange={handleModifyPrice}
                                      ></input>
                                      <Select
                                        labelId="buyer_label"
                                        id="buyer"
                                        value={
                                          userOwner
                                            ? userOwner
                                            : player.purchased_by
                                        }
                                        onChange={handleModifyNewOwnerUser}
                                        className="modifyUser"
                                      >
                                        {Object.keys(users).map(function (
                                          keyName,
                                          keyIndex
                                        ) {
                                          return (
                                            <MenuItem
                                              key={keyName}
                                              value={keyName}
                                            >
                                              {users[keyName].team}
                                            </MenuItem>
                                          );
                                        })}
                                      </Select>
                                    </div>
                                    {confirmModButton && (
                                      <div
                                        style={{
                                          textAlign: "center",
                                          marginTop: "10px",
                                        }}
                                      >
                                        <button
                                          onClick={modifyPlayer}
                                          className="primaryButton"
                                        >
                                          Conferma
                                        </button>
                                      </div>
                                    )}
                                  </>
                                )}
                                {fantauser.role === "user" && (
                                  <div>
                                    <h4>
                                      Calciatore di{" "}
                                      {users[player.purchased_by].team}
                                    </h4>
                                    <span>Valore di acquisto:</span>
                                    <h2 style={{ marginTop: "2px" }}>
                                      <span style={{ fontSize: "70px" }}>
                                        {player.purchased_for}
                                      </span>{" "}
                                      mln
                                    </h2>
                                  </div>
                                )}
                                {errorString !== "" && (
                                  <p
                                    className="error"
                                    style={{ marginTop: "10px" }}
                                  >
                                    {errorString}
                                  </p>
                                )}
                                {successString !== "" && (
                                  <p
                                    className="success"
                                    style={{ marginTop: "10px" }}
                                  >
                                    {successString}
                                  </p>
                                )}
                              </div>
                              <div className="actions mt-3">
                                <h4>Svincola</h4>
                                <p
                                  style={{
                                    fontSize: "12px",
                                    marginBottom: "10px",
                                  }}
                                >
                                  Libera il giocatore al prezzo d'acquisto
                                </p>
                                <button
                                  onClick={() => {
                                    setPlayerFree(
                                      player.id,
                                      player.name,
                                      player.purchased_for
                                    );
                                  }}
                                  className="primaryButton"
                                  style={{ marginTop: "10px", width: "100%" }}
                                >
                                  Svincola
                                </button>
                              </div>
                            </div>
                          )}
                        </Row>
                        <div className="info d-flex justify-content-around"></div>
                      </Col>
                      <Col xs={12} lg={5}>
                        {(player.purchased_by === "" ||
                          !player.purchased_by) && (
                          <div className="competitors">
                            <h5 style={{ margin: "0" }}>
                              Fantallenatori in gara
                            </h5>
                            <ul>
                              {Object.keys(users).map(function (
                                keyName,
                                keyIndex
                              ) {
                                // let maxOffer = getMaxOfferFromUser(
                                //   keyName,
                                //   players,
                                //   settings.user_mln,
                                //   settings.min_players
                                // );
                                // let playersLeft = getAmountOfPlayerLeftToBuyForUser(
                                //   keyName,
                                //   players,
                                //   settings.min_players
                                // );

                                const maxOfferForMinPlayer =
                                  getMaxOfferFromUser(
                                    keyName,
                                    players,
                                    settings.user_mln,
                                    settings.min_players
                                  );

                                const amountLeftForPlayer =
                                  getAmountLeftForPlayer(
                                    keyName,
                                    players,
                                    settings.user_mln
                                  );

                                const minPlayersLeft =
                                  getAmountOfPlayerLeftToBuyForUser(
                                    keyName,
                                    players,
                                    settings.min_players
                                  );

                                return (
                                  <li
                                    // className="competitor"
                                    key={keyName}
                                    className={
                                      canUserBuyPlayer(
                                        valueInputValue,
                                        settings,
                                        players,
                                        keyName
                                      )
                                        ? "active"
                                        : "not-active"
                                    }
                                  >
                                    <div className="indicator"></div>
                                    {users[keyName].team}
                                    <span
                                      style={{
                                        marginLeft: "3px",
                                        fontSize: "12px",
                                      }}
                                    >
                                      (
                                      {settings.min_players ===
                                      settings.max_players
                                        ? maxOfferForMinPlayer
                                        : parseInt(minPlayersLeft) <= 0
                                        ? amountLeftForPlayer
                                        : maxOfferForMinPlayer}
                                      )
                                    </span>
                                  </li>
                                );
                              })}
                            </ul>
                          </div>
                        )}
                        {player.purchased_by !== "" && (
                          <div className="d-flex justify-content-center">
                            <img
                              width="270"
                              alt="sticker"
                              src={
                                require("../../Assets/stickers/" + sticker)
                                  .default
                              }
                            />
                          </div>
                        )}
                      </Col>
                    </Row>
                  </TabPanel>
                  {/* <TabPanel value={tabValue} index={1}>
                    <PlayerStatistics player={player} />
                  </TabPanel> */}
                </Box>
              </Col>
            </Row>
          </div>
        </div>

        <Row>
          <Col>
            {fantauser &&
              (fantauser.role === "admin" ||
                fantauser.role === "superAdmin") && (
                <div className="shuffleWrapper">
                  <button className="primaryButton" onClick={shufflePlayer}>
                    Estrai di nuovo
                    <svg
                      width="15"
                      height="15"
                      viewBox="0 0 480.026 480.026"
                      style={{ marginLeft: "5px" }}
                    >
                      <path d="M475.922 229.325l-144-160c-3.072-3.392-7.36-5.312-11.904-5.312h-96c-6.304 0-12.032 3.712-14.624 9.472-2.56 5.792-1.504 12.544 2.72 17.216l134.368 149.312-134.368 149.28c-4.224 4.704-5.312 11.456-2.72 17.216 2.592 5.792 8.32 9.504 14.624 9.504h96c4.544 0 8.832-1.952 11.904-5.28l144-160c5.472-6.08 5.472-15.36 0-21.408zm-208 0l-144-160c-3.072-3.392-7.36-5.312-11.904-5.312h-96c-6.304 0-12.032 3.712-14.624 9.472-2.56 5.792-1.504 12.544 2.72 17.216l134.368 149.312L4.114 389.293c-4.224 4.704-5.312 11.456-2.72 17.216 2.592 5.792 8.32 9.504 14.624 9.504h96c4.544 0 8.832-1.952 11.904-5.28l144-160c5.472-6.08 5.472-15.36 0-21.408z" />
                    </svg>
                  </button>
                </div>
              )}
          </Col>
        </Row>
      </div>
    </div>
  );
};

export default Player;
