import React, { useState, useEffect } from "react";

// Import COmponent
import Strike from "../../UI/Strike";

// Import classes
import classes from "./PlayerStatistic.module.css";

import {
  LineChart,
  Line,
  Bar,
  AreaChart,
  Area,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend,
  ResponsiveContainer,
  ComposedChart,
} from "recharts";

// import helpers
import { getMarkboxColor } from "../../../Helpers/helpers";

// Import bootsraps
import { Row, Col } from "react-bootstrap";

// Import dependencies
import { useSelector } from "react-redux";

// Import helpers
import {
  getGraphData,
  sumStatistic,
  getCleanDataByGivenParam,
  getColorOfStatistic,
  getMaxNumberOfMatches,
} from "../../../Helpers/playerStatisticsChartData";

import { getBestRoleForPlayer } from "../../../Helpers/helpers";

function PlayerStatistic(props) {
  const [graphdata, setGraphdata] = useState([]);
  const [graphSelections, setGraphSelections] = useState([
    {
      label: "Media Voto",
      shortname: "MV",
      value: "mv",
      field: "mark",
      active: true,
    },
    {
      label: "Media fanta Voto",
      shortname: "MFV",
      value: "mfv",
      field: "fm",
      active: false,
    },
    {
      label: "Goals",
      shortname: "Goals",
      value: "goals",
      field: "goals",
      active: false,
      displayNot: "P",
    },
    {
      label: "Assist",
      shortname: "Assist",
      value: "assists",
      field: "assists",
      active: false,
      displayNot: "P",
    },
    {
      label: "Autogoals",
      shortname: "Autogoal",
      value: "autogoals",
      field: "autogoals",
      active: false,
    },
    {
      label: "C. Gialli",
      shortname: "C. Gialli",
      value: "yellow_cards",
      field: "yellow_cards",
      active: false,
    },
    {
      label: "C. Rossi",
      shortname: "C. Rossi",
      value: "red_cards",
      field: "red_cards",
      active: false,
    },
    {
      label: "Goal Subiti",
      shortname: "G. Subiti",
      value: "goals_conceded",
      field: "goals_conceded",
      active: false,
      displayOnly: "P",
    },
    {
      label: "Rigori Parati",
      shortname: "R. Parati",
      value: "saved_penaltied",
      field: "saved_penaltied",
      active: false,
      displayOnly: "P",
    },
    {
      label: "Rigori Segnati",
      shortname: "R. Segnati",
      value: "scored_penalties",
      field: "scored_penalties",
      active: false,
      displayNot: "P",
    },
    {
      label: "Rigori Sbagliati",
      shortname: "R. Sbagliati",
      value: "missed_penalties",
      field: "missed_penalties",
      active: false,
      displayNot: "P",
    },
  ]);

  console.log("props", props);

  const switchGraph = (graphtype) => {
    setGraphdata(getGraphData(props.data, graphtype, props.season));

    // Handle active
    let newgraphSelection = [];
    graphSelections.forEach((element) => {
      element.value === graphtype
        ? (element.active = true)
        : (element.active = false);
      newgraphSelection.push({ ...element });
    });
    console.log(graphSelections);
    console.log(newgraphSelection);
    setGraphSelections(newgraphSelection);
  };

  useEffect(() => {
    setGraphdata(getGraphData(props.data, "mv", props.season));
  }, []);

  return (
    <div className="mt-2">
      <div className="my-3">
        <div
          className={`${classes.etiq} statisticBadge ${getColorOfStatistic(
            getBestRoleForPlayer(props.player),
            "match_played",
            sumStatistic(
              getCleanDataByGivenParam(
                props.data,
                props.player.id,
                props.season
              ),
              "pg"
            ),
            props.season === "all" ? getMaxNumberOfMatches(props.data) : 38
          )}`}
        >
          <div className={classes.label}>P. Giocate</div>
          <div className={classes.content}>
            <span>
              {sumStatistic(
                getCleanDataByGivenParam(
                  props.data,
                  props.player.id,
                  props.season
                ),
                "pg"
              )}
            </span>
          </div>
        </div>
        {graphSelections.length > 0 &&
          graphSelections.map((element) => {
            if (
              props.player.macrorole !== element.displayNot &&
              (!element.displayOnly ||
                element.displayOnly === props.player.macrorole)
            ) {
              return (
                <div
                  className={`${
                    classes.etiq
                  } statisticBadge ${getColorOfStatistic(
                    getBestRoleForPlayer(props.player),
                    element.field,
                    sumStatistic(
                      getCleanDataByGivenParam(
                        props.data,
                        props.player.id,
                        props.season
                      ),
                      element.field
                    ),
                    props.season === "all"
                      ? getMaxNumberOfMatches(props.data)
                      : 38
                  )}`}
                >
                  <div className={classes.label}>{element.shortname}</div>
                  <div className={classes.content}>
                    <span>
                      {sumStatistic(
                        getCleanDataByGivenParam(
                          props.data,
                          props.player.id,
                          props.season
                        ),
                        element.field
                      )}
                    </span>
                  </div>
                </div>
              );
            }
          })}
      </div>
      <Strike content="Distribuzione" />

      <div>
        {graphSelections.length > 0 &&
          graphSelections.map((element) => {
            if (
              props.player.macrorole !== element.displayNot &&
              (!element.displayOnly ||
                element.displayOnly === props.player.macrorole)
            ) {
              return (
                <button
                  className={`
                ${classes.statisticHandler} ${
                    element.active ? classes.active : ""
                  }`}
                  onClick={() => {
                    switchGraph(element.value);
                  }}
                >
                  {element.label}
                </button>
              );
            }
          })}
      </div>
      <div className={classes.graphWrapper}>
        {graphdata.data && graphdata.data.length > 0 && (
          <ResponsiveContainer width="100%" height={400}>
            <ComposedChart
              width={730}
              height={250}
              data={graphdata.data}
              margin={{ top: 10, right: 30, left: 0, bottom: 0 }}
            >
              <defs>
                <linearGradient id="colorUv" x1="0" y1="0" x2="0" y2="1">
                  <stop offset="5%" stopColor="#8884d8" stopOpacity={0.85} />
                  <stop offset="95%" stopColor="#8884d8" stopOpacity={0.4} />
                </linearGradient>
              </defs>
              <XAxis dataKey="name" />
              <YAxis />
              <CartesianGrid strokeDasharray="3 3" />
              <Tooltip />
              {graphdata.graphtype === "area" && (
                <Area
                  type="linear"
                  dataKey={graphdata.datakey}
                  stroke="#8884d8"
                  fillOpacity={10}
                  fill="url(#colorUv)"
                  strokeWidth={3}
                  activeDot={{ r: 8 }}
                />
              )}
              {graphdata.graphtype === "line" && (
                <Line
                  type="linear"
                  dataKey={graphdata.datakey}
                  stroke="#8884d8"
                  fill="black"
                  strokeWidth={3}
                  activeDot={{ r: 8 }}
                />
              )}
              {graphdata.graphtype === "bar" && (
                <Bar dataKey={graphdata.datakey} fill="#8884d8" barSize={50} />
              )}
            </ComposedChart>
          </ResponsiveContainer>
        )}
      </div>
    </div>
  );
}

export default PlayerStatistic;
