import * as React from "react";

// Import dependencies
import { useSelector } from "react-redux";

// Import helpers
import { getFlagCode } from "../../../Helpers/helpers";

function PlayerCard(props) {
  // add useselector hook for data
  const settings = useSelector((state) => (state ? state.settings : ""));

  // Import imaged
  const cardBackground = require("../../../Assets/cardBg7.png");

  // Get mantra roles Array
  const mantraRolesAry = props.player.role.split(";");
  return (
    <div
      className={"playerCard " + props.player.team}
      style={{ backgroundImage: `url("${cardBackground.default}")` }}
    >
      <div className={"overlay " + props.player.team}></div>
      <div className={"overlay2"}>
        <div className={"flagIcon d-none"}>
          <span>
            <img
              src={`https://flagcdn.com/h40/${getFlagCode(
                props?.player?.nationality?.split(",")[0]
              )}.png`}
              srcSet={`https://flagcdn.com/h40/${getFlagCode(
                props?.player?.nationality?.split(",")[0]
              )}.png 2x,
https://flagcdn.com/h60/${getFlagCode(
  props?.player?.nationality?.split(",")[0]
              )}.png 3x`}
              alt={props.player.nationality}
            />
          </span>
        </div>
        <div className={"flagIcon"} title="Valore attuale">
          <span>
            {props?.player?.marketvalue}
          </span>
        </div>
        <div className={"teamIcon"}>
          <span>
            <img
              style={{
                maxWidth: "30px",
                maxHeight: "30px",
              }}
              src={
                require("../../../Assets/stemmi/" + props?.player?.team + ".png")
                  .default
              }
              alt=""
            />
          </span>
        </div>
        <div className={"playerImage"}>
          <div className="blob">
            <img src={props?.player?.image} alt="" />
          </div>
        </div>
        <div className={"playerInfoWrapper"}>
          <span className={"playerName"}>{props?.player?.name}</span>
          <div className={"playerInfo mt-2 d-none"} >
            <div className="infoWrapper">
              <span className="wrapperTitle">Età</span>
              <span className={"star"}>{props?.player?.age}</span>
            </div>
            <div className="infoWrapper">
              <span className="wrapperTitle">Val</span>
              <span className={"star"}>{props?.player?.q_a}</span>
            </div>
          </div>
          <div className={"playerInfo mt-3"}>
            <div className={"playerRolesWrapper infoWrapper"}>
              <span className="wrapperTitle">Ruolo</span>
              {settings.game_type === "mantra" ? (
                <div className="d-flex">
                  {mantraRolesAry.map((element) => {
                    return (
                      <span
                        key={props?.player?.id + element.toLowerCase()}
                        className={"roleBadge " + element.toLowerCase()}
                      >
                        {element}
                      </span>
                    );
                  })}
                </div>
              ) : (
                <span
                  className={
                    "roleBadge " + props.player.macrorole.toLowerCase()
                  }
                >
                  {props.player.macrorole}
                </span>
              )}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default PlayerCard;
