import React, { useState, useEffect } from "react";
// Import dependencies
import { useSelector } from "react-redux";

// Import bootsraps
import { Row, Col } from "react-bootstrap";

// Import classes
import classes from "./PlayerStatistics.module.css";

// Import
import PlayerStatistic from "./PlayerStatistic";

// import helpers
import { getMarkboxColor } from "../../../Helpers/helpers";

// IMport material UI
import PropTypes from "prop-types";
import Tabs from "@mui/material/Tabs";
import Tab from "@mui/material/Tab";
import Typography from "@mui/material/Typography";
import Box from "@mui/material/Box";
import TabPanel from "../../UI/TabPanel";

// Import helpers for data chart
import {
  getMV,
  getCleanStatsDataById,
  getAvailableSeasonsByListOfStats,
} from "../../../Helpers/playerStatisticsChartData";

function PlayerStatistics(props) {
  // Set hooks
  const [statsData, setStatsData] = useState([]);
  const [availableSeasons, setAvailableSeasons] = useState([]);
  const [tabValue, setTabValue] = useState(0);
  useEffect(() => {
    getCleanStatsDataById(props.player.id, "all", setStatsData);
  }, []);
  useEffect(() => {
    setAvailableSeasons(getAvailableSeasonsByListOfStats(statsData));
  }, [statsData]);
  // Set functions
  const handleTabChange = (event, newValue) => {
    setTabValue(newValue);
  };
  function a11yProps(index) {
    return {
      id: `simple-tab-${index}`,
      "aria-controls": `simple-tabpanel-${index}`,
    };
  }
  // Set graph data
  return (
    <div className={classes.playerstats}>
      <Box sx={{ width: "100%", padding: "0px !important" }}>
        <Box
          sx={{
            borderBottom: 1,
            borderColor: "divider",
            padding: "0px !important",
          }}
        >
          <Tabs
            value={tabValue}
            onChange={handleTabChange}
            aria-label="basic tabs example"
          >
            {availableSeasons.length && statsData.length &&
              availableSeasons.map((element, index) => {
                return (
                  <Tab
                    sx={{
                      padding: "20px !important",
                    }}
                    label={element}
                    {...a11yProps(index)}
                  />
                );
              })}
            <Tab
              sx={{
                padding: "20px !important",
              }}
              label="Complessivo"
              {...a11yProps(availableSeasons.length)}
            />
          </Tabs>
        </Box>
        {availableSeasons.length && statsData.length &&
          availableSeasons.map((element, index) => {
            return (
              <TabPanel value={tabValue} index={index}>
                <PlayerStatistic player={props.player} data={statsData} season={element} />
              </TabPanel>
            );
          })}
        <TabPanel value={tabValue} index={availableSeasons.length}>
          <PlayerStatistic player={props.player} data={statsData} season="all" />
        </TabPanel>
      </Box>
      {availableSeasons &&
        availableSeasons.map((element) => {
          return (
            <Tab
              sx={{
                padding: "20px !important",
                color: "white !important",
              }}
              label="Asta"
              {...a11yProps(0)}
            />
          );
        })}
    </div>
  );
}

export default PlayerStatistics;
