// Import react dependecies
import React, { useState, useRef } from "react";
import classes from "./HandleFootbalPlayers.module.css";
import { useSelector, useDispatch } from "react-redux";
import * as XLSX from "xlsx";
import { transformPlayersListIntoArray } from "../../Helpers/helpers";

import { getPlayers } from "../../Data/firebaseconfig/players/read";
import { createPlayers } from "../../Data/firebaseconfig/players/create";
import { createPlayerStats } from "../../Data/firebaseconfig/playersStats/create";

const calculateFantamedia = (
  mark,
  goals,
  assists,
  autogoals,
  missedPenalties,
  savedPenalties,
  yellowCard,
  redCard,
  goalsConceded
) => {
  const fm =
    parseFloat(mark) +
    parseInt(goals) * 3 +
    parseInt(assists) * 1 +
    parseInt(savedPenalties) * 3 -
    parseInt(autogoals) * 2 -
    parseInt(missedPenalties) * 3 -
    parseFloat(parseInt(yellowCard) * 0.5).toFixed(2) -
    parseFloat(redCard) * 1 -
    parseFloat(goalsConceded) * 1;

  return fm;
};

const HandleFootbalPlayers = (props) => {
  // Get player state
  const currentPlayers = useSelector((state) =>
    state.newPlayers ? Object.values(state.newPlayers) : {}
  );

  const currentPlayerStats = useSelector((state) => state.playerStats);

  let currentStats = { ...currentPlayerStats };

  const dispatch = useDispatch();

  const onBasicImport = (e) => {
    const [file] = e.target.files;
    const reader = new FileReader();

    // props.setLoaderIsShown(true);

    try {
      reader.onload = (evt) => {
        const bstr = evt.target.result;
        const wb = XLSX.read(bstr, { type: "binary" });
        const wsname = wb.SheetNames[0];
        const ws = wb.Sheets[wsname];
        const data = XLSX.utils.sheet_to_csv(ws, { header: 1 });
        const rawArray = data.split("\n");
        let newPlayersObject = {};
        let includedIDs = [];
        rawArray.forEach((element, index) => {
          if (index > 1) {
            let playerArray = element.split(",");
            const id = playerArray[0];
            let player = {
              id: id,
              macrorole: playerArray[1],
              role: playerArray[2],
              name: playerArray[3],
              // fullname: "",
              team: playerArray[4],
              q_a: parseInt(playerArray[5]),
              q_i: parseInt(playerArray[6]),
              // image: "",
              // card_image: "",
              // update_at: "",
              // age: "",
              // nationality: "",
              // played_match: 0,
              // goals: 0,
              // goals_conceded: 0,
              // assist: 0,
              // yellow_cards: 0,
              // red_cards: 0,
              // average_score: 0,
              // average_fantascore: 0,
              is_active: true,
            };
            includedIDs.push(parseInt(id));
            // Check if ID does not exist on the OLD players
            const exist = currentPlayers.find(
              (element) => parseInt(element.id) === parseInt(id)
            );
            if (!exist) {
              newPlayersObject[id] = player;
            } else {
              newPlayersObject[id] = {
                ...exist,
                macrorole: player.macrorole,
                role: player.role,
                team: player.team,
                q_a: player.q_a,
                q_i: player.q_i,
              };
            }
          }
        });

        // filter the other records
        const toBeExcluded = currentPlayers.filter((element) => {
          return !includedIDs.includes(element.id);
        });

        toBeExcluded.forEach((element) => {
          newPlayersObject[element.id] = {
            ...element,
            is_active: false,
          };
        });

        // Create and push
        createPlayers(newPlayersObject).then((data) => {
          console.log(data);
        });
      };
      reader.readAsBinaryString(file);
      // props.setLoaderIsShown(false);
    } catch (error) {
      console.error(error);
    }
  };

  const onStatsImport = (e) => {
    const files = e.target.files;
    let oldData = [];
    // props.setLoaderIsShown(true);

    for (var key of Object.keys(files)) {
      const file = files[key];
      let name = file.name;
      // Get season and round
      const nameWithoutExt = name.split(".")[0];
      const nameWithoutExtAry = nameWithoutExt.split("_");
      const season = nameWithoutExtAry[3];
      const round = nameWithoutExtAry[5];
      // try {
      const reader = new FileReader();
      reader.onload = (evt) => {
        const bstr = evt.target.result;
        const wb = XLSX.read(bstr, { type: "binary" });
        const wsname = wb.SheetNames[0];
        const ws = wb.Sheets[wsname];
        const data = XLSX.utils.sheet_to_csv(ws, { header: 1 });
        const rawArray = data.split("\n");

        // Set var in cycle
        let blocksToUpdate = [];
        let playerTeam = "";
        let stats = {};

        rawArray.forEach((rawRow, index) => {
          if (index > 4) {
            let playerData = {};
            let playerArray = rawRow.split(",");
            // Jump the firts rows
            if (!isNaN(playerArray[0])) {
              // Get just the players
              if (playerArray[1] === "ALL") {
                // It's a coach
                // We are arrived at the end of the cycle. It mesans it's a new block
                blocksToUpdate.forEach((i) => {
                  oldData[i].coach = playerArray[2];
                  oldData[i].team = playerTeam;
                });
                playerTeam = "";
                blocksToUpdate = [];
              } else {
                playerData.id = parseInt(playerArray[0]);
                playerData.name = playerArray[2];
                playerData.mark = parseFloat(playerArray[3].replace("*", ""));
                playerData.goals =
                  parseInt(playerArray[4]) + parseInt(playerArray[8]);
                playerData.goals_conceded = parseInt(playerArray[5]);
                playerData.saved_penaltied = parseInt(playerArray[6]);
                playerData.missed_penalties = parseInt(playerArray[7]);
                playerData.scored_penalties = parseInt(playerArray[8]);
                playerData.autogoals = parseInt(playerArray[9]);
                playerData.yellow_cards = parseInt(playerArray[10]);
                playerData.red_cards = parseInt(playerArray[11]);
                playerData.assists = parseInt(playerArray[12]);
                playerData.season = season;
                playerData.round = round;
                playerData.fm = calculateFantamedia(
                  playerData.mark,
                  playerData.goals,
                  playerData.assists,
                  playerData.autogoals,
                  playerData.missed_penalties,
                  playerData.saved_penaltied,
                  playerData.yellow_cards,
                  playerData.red_cards,
                  playerData.goals_conceded
                );
              }
              oldData.push(playerData);
              blocksToUpdate.push(oldData.length - 1);
            } else if (playerArray[0] !== "Cod.") {
              playerTeam = playerArray[0];
            }
            if (playerData.id) {
              // Check if Player ID is present on our dataset
              // If nort, is a player statistic that we don't care
              const exist = currentPlayers.find((element) => {
                return parseInt(playerData.id) === parseInt(element.id);
              });
              if (exist) {
                const playerStatExist = Object.values(currentPlayerStats).find((element) => {
                  return (
                    parseInt(playerData.id) === parseInt(element.id) &&
                    parseInt(playerData.round) === parseInt(element.round) &&
                    playerData.season === element.season
                  );
                });
                if (!playerStatExist) {
                  console.log(currentStats)
                  currentStats.push(playerData);
                }
              }
            }
          }
        });

        createStatsBlock(currentStats);
      };
      reader.readAsBinaryString(file);
    }
  };

  const createStatsBlock = (data) => {
    createPlayerStats({}).then((result) => {
      dispatch({
        type: "set_player_stats",
        data: data,
      });
    });
  };

  return (
    <div>
      <h2>Importa calciatori</h2>
      <div className={classes.importFieldWrapper}>
        <input
          type="file"
          onChange={onBasicImport}
          className={classes["custom-file-input"]}
        />
      </div>

      <h2>Importa statistiche</h2>
      <div className={classes.importFieldWrapper}>
        <input
          type="file"
          name="files[]"
          multiple="multiple"
          onChange={onStatsImport}
          className={classes["custom-file-input"]}
        />
      </div>
    </div>
  );
};

export default HandleFootbalPlayers;
